import { Component, OnInit, Output, Input, EventEmitter, ViewChild, TemplateRef, HostBinding, NgZone } from '@angular/core';
import { LocaleService } from '@farris/ui-locale';
import { NotifyService } from '@farris/ui-notify';
import { TreeNode, TreeTableComponent } from '@farris/ui-treetable';

@Component({
    selector: 'app-select-help-text-field',
    templateUrl: './select-help-text-field.component.html'
})
export class SelectHelpTextFieldComponent implements OnInit {
    @Output() closeModal = new EventEmitter<any>();
    @Output() submitModal = new EventEmitter<any>();
    @Input() value;
    @Input() helpId = '';
    @Input() schemaData;
    @ViewChild('modalFooter') modalFooter: TemplateRef<any>;

    @HostBinding('class')
    class = 'd-flex f-utils-fill-flex-column h-100';

    modalConfig = {
        title: 'å­æ®µéæ©å¨',
        width: 900,
        height: 500,
        showButtons: true,
        showMaxButton: false
    };

    // æ è¡¨æ°æ®
    helpTreeData: TreeNode[] = [];
    // æ è¡¨åéç½®
    treeCols = [{ field: 'name', title: 'åç§°' }, { field: 'label', title: 'ç¼å·' }, { field: 'bindingPath', title: 'ç»å®å­æ®µ' }];
    fieldName = 'åç§°';
    fieldLabel = 'ç¼å·';
    fieldTitle = 'ç»å®å­æ®µ';
    fieldError = 'å·²ç»å®å­æ®µä¸å­å¨ï¼';
    // æ è¡¨å®ä¾
    @ViewChild('treeTable') treeTable: TreeTableComponent;

    constructor(private notifyService: NotifyService, private ngZone: NgZone,private localeService: LocaleService) { }

    ngOnInit(): void {
        this.fieldName = this.localeService.getValue('lookupConfig.name');
        this.fieldLabel = this.localeService.getValue('lookupConfig.code');
        this.fieldTitle = this.localeService.getValue('lookupConfig.bindingPath');
        this.fieldError = this.localeService.getValue('lookupConfig.fieldError');
        this.treeCols = [{ field: 'name', title: this.fieldName }, { field: 'label', title: this.fieldLabel }, { field: 'bindingPath', title: this.fieldTitle }];
        if (!this.schemaData) {
            return;
        }
        this.getHelpFields(this.schemaData);
    }
    /**
     * è·åå¸®å©schemaå­æ®µ
     * @param schema schema
     */
    getHelpFields(schema) {
        if (!schema || !schema.entities || schema.entities.length === 0) {
            return;
        }
        const mainTable = schema.entities[0];
        if (mainTable.type && mainTable.type.fields) {
            this.helpTreeData = this.assembleFields2Tree(mainTable.type.fields);
        }
        this.ngZone.runOutsideAngular(() => {
            setTimeout(() => {
                if (!this.value) { return; }
                try {
                    this.treeTable.selectNode(this.value);
                } catch (e) {
                    this.treeTable.clearSelections();
                    this.notifyService.error(this.fieldError);
                }
            });
        });
    }
    /**
     * schemaå­æ®µéåç»è£ææ 
     * @param fields schemaå­æ®µéå
     */
    assembleFields2Tree(fields) {
        const treeData = [];
        fields.forEach(element => {
            // å³èè¡¨å­æ®µ / UDTå­æ®µ
            let children = [];
            if (element.type && element.type.fields && element.type.fields.length > 0) {
                children = this.assembleFields2Tree(element.type.fields);

            }
            // ééæ§çå¸®å©åæ°æ®æ²¡æbindingPathå±æ§
            if (!element.bindingPath) {
                element.bindingPath = element['parentPath'] ? element['parentPath'] + '.' + element.label : element.label;
            }
            treeData.push({
                data: element,
                children,
                expanded: true,
                selectable: children.length > 0 ? false : true
            });
        });
        return treeData;
    }

    /**
     * åæ¶
     */
    clickCancel() {
        this.closeModal.emit();
    }
    /**
     * ç¡®å®
     */
    clickConfirm() {
        if (!this.treeTable.selectedRow) {
            this.notifyService.warning('è¯·éæ©å­æ®µ');
            return;
        }
        const selectedData = this.treeTable.selectedRow.data;
        this.submitModal.emit({ value: selectedData.bindingPath, name: selectedData.name });
    }
}
