import { Injectable, Injector } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { RemoteParams, ILookupHttpService } from '@farris/ui-lookup';
import { NotifyService } from '@farris/ui-notify';
import { LoadingService } from '@farris/ui-loading';

@Injectable()
export class LookupDataService implements ILookupHttpService {
  constructor(
    public http: HttpClient,
    private injector: Injector,
    private notifyService: NotifyService,
    private loading: LoadingService
  ) {
  }

  getData(url, params: RemoteParams): Observable<any> {
    if (!params || !params.helpId) {
      this.notifyService.error('helpIdä¸è½ä¸ºç©º');
      this.loading.clearAll();
      return;
    }
    const _url = `/api/runtime/bcc/v1.0/helpconfig/data/${params.helpId}`;

    params = Object.assign(params || {}, { withSpecificField: true });
    return this.http.get(_url, { params: { queryParam: JSON.stringify(params) } });
  }
}
