import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'farris-lookup-config-metadata',
  templateUrl: './lookup-config-metadata.component.html'
})
export class LookupConfigMetaDataComponent implements OnInit {

  // metadataPath = '';
  HelpMetadata = 'HelpMetadata';
  @Input() metadataPath;
  @Input() isDeisgnerEnv:boolean = false;
  @Output('selectedMeta') selectedMeta = new EventEmitter();
  @Output('closeModal') closeModal = new EventEmitter();
  constructor() { }

  ngOnInit() {
  }

  selectedMetadata(event){
    this.selectedMeta.emit(event);
  }
  closeModalEmitter(){
    this.closeModal.emit();
  }

}
